import { Component, OnInit } from '@angular/core';

import { SharedService } from '../services/shared.service';
import { MapService } from '../services/map.service';
import { Location } from '../models/location'

@Component({
  selector: 'coc-search-box',
  templateUrl: './search-box.component.html'
})
export class SearchBoxComponent implements OnInit {
  
  isListView: boolean;
  isMapView: boolean;
  businessResults = this.mapService.data;
  retail = this.mapService.retailSubCategories;
  finance = this.mapService.financeSubCategories;
  restaurant = this.mapService.restaurantSubCategories;
  licenseType = this.mapService.licenseType;
  filterOptions: any[] = [];
  licenseFilterButtonText: string = "Show All";
  searchAddress: string;
  response: Location[]; 

  updateFilterOptions(e) {
    if(e.target.checked) {
      this.filterOptions.push(e.target.value);
    } else {
      if(this.filterOptions.includes(e.target.value)) {
        for(let i = 0; i < this.filterOptions.length; i++) {
          if(e.target.value == this.filterOptions[i]) {
            this.filterOptions.splice(i, 1);
          }
        }
      }
    }
  }
  
  updateLicenseFilter(e) {
    this.licenseFilterButtonText = e.target.innerHTML;
  }

  updateSearchAddress(address) {
    this.mapService.setSearchAddress(address);
  }

  checkAllFinance(e) {
    if(e.target.checked) {
      for(let i = 0; i < this.finance.length; i++) {
        this.filterOptions.push(this.finance[i].name)
        this.finance[i].state = e.target.checked
      }
    } else {
      this.finance.forEach(x => x.state = e.target.checked)
    }
  }
  
  isAllCheckedFinance() {
    return this.finance.every(_ => _.state);
  }

  checkAll(ev) {
    this.restaurant.forEach(x => x.state = ev.target.checked);
  }

  isAllChecked() {
    return this.restaurant.every(_ => _.state);
  }

  checkAllRetail(ev) {
    this.retail.forEach(x => x.state = ev.target.checked);
  }

  isAllRetailChecked() {
    return this.retail.every(_ => _.state);
  }

  toggleListView() {
    this.sharedService.setView(!this.isListView);
  }

  loadData() {
    let unfilteredResults = this.mapService.data;
    if(this.filterOptions.length) {
      let filteredResults = this.businessResults.filter((result) => {
        for(let i = 0; i < this.filterOptions.length; i++) {
          if(result.subCategory == this.filterOptions[i]) {
            return result;
          } 
        }
      })
      this.mapService.setBusinessResults(filteredResults);
    } else {
      this.mapService.setBusinessResults(unfilteredResults);
    }
    if(this.isMapView) { this.toggleListView(); }
  }

  search(e) {
    e.preventDefault();
    if(!this.searchAddress) {
      alert('Please type in a valid address.')
      return;
    }

    this.mapService.getAddress(this.searchAddress).subscribe(
      response => {
        if(response.candidates.length) {
          this.mapService.setSearchCoordinates(response);
          this.loadData();
        } else {
          alert('Not a valid address. Please try again.');
        }
      }
    );
  }

  constructor(
    public sharedService: SharedService,
    private mapService: MapService) { 
  }

  ngOnInit() {
    
    this.sharedService.getView$.subscribe(
      isListView => {
        this.isListView = isListView;
      }
    )

    this.mapService.currentResults$.subscribe(
      businessesResults => {
        this.businessResults = businessesResults;
      }
    )

    this.sharedService.getCurrentView$.subscribe(
      isMapView => {
        this.isMapView = isMapView;
      }
    )

    this.mapService.currentAddress$.subscribe(
      searchAddress => {
        this.searchAddress = searchAddress;
      }
    )
  }
}
