import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Subscription } from 'rxjs';

import { SharedService } from '../services/shared.service';
import { MapService } from '../services/map.service';


@Component({
  selector: 'coc-list-view',
  templateUrl: './list-view.component.html'
})
export class ListViewComponent implements OnInit {
    
  private isActive: boolean;
  private isListView: boolean;
  private isMapView: boolean;
  private businessResults: any[] = [];
  private selectedBusiness: any;
  private searchAddress: string = "175 S Arizona Ave, Chandler, AZ 85225";

  toggleActiveState(result) {
    //Get the coordinates
    let coordinates = [result.coordinates[0], result.coordinates[1]];
    //Turn off active state for everybody
    this.businessResults.map((result) => {
      return result.isActive = false;
    });
    //Turn on active state for what was clicked
    result.isActive = true;
    //Pan to business
    this.mapService.panToBusiness(coordinates)
  }

  toggleListView() {
    if(this.isMapView && this.businessResults.length > 0) { 
      this.sharedService.setView(!this.isListView);
    } else {
      this.sharedService.toggleView(!this.isMapView); 
      this.sharedService.setView(!this.isListView);
    }
  }

  exportResults() {
    alert("Results exported 🖨️");
  }

  constructor(
    private sharedService: SharedService,
    private mapService: MapService
    ) { }

  ngOnInit() {
    
    this.sharedService.getState$.subscribe(
      isActive => {
        this.isActive = isActive;
      }
    )

    this.sharedService.getCurrentView$.subscribe(
      isMapView => {
        this.isMapView = isMapView;
      }
    )

    this.sharedService.getView$.subscribe(
      isListView => {
        this.isListView = isListView;
      }
    )
    
    this.mapService.currentResults$.subscribe(
      businessesResults => {
        this.businessResults = businessesResults;
      }
    )

    this.mapService.currentBusiness$.subscribe(
      selectedBusiness => {
        this.selectedBusiness = selectedBusiness;
      }
    )

    this.mapService.currentAddress$.subscribe(
      searchAddress => {
        this.searchAddress = searchAddress;
      }
    )
  }

  

}